
// 
export const fields = {
  email: [
    { required: true, message: ' Email ', trigger: 'blur' },
    { type: 'email', message: ' Email ', trigger: 'blur' },
  ],

  username: [
    { required: true, message: '', trigger: 'blur' },
    { pattern: /^[0-9a-z_-]+$/i, message: ' "-"  "_"' },
    { min: 5, max: 16, message: ' 5  16 ' },
  ],

  password: [
    { required: true, message: '', trigger: 'blur' },
    { min: 6, max: 16, message: ' 6-16 ' },
    { validator (rule, value, callback) {
      if (value.match(/^(?:\d+|[a-zA-Z]+|[!@#$%^&*]+)$/)) callback()
      else callback(new Error(''))
    } },
  ],

  agreement: [
    { required: true, type: 'boolean', message: '' },
    { validator (rule, value, callback) {
      if (value === true) callback()
      else callback(new Error(''))
    } },
  ],
}
